<script async src="//busuanzi.ibruce.info/busuanzi/2.3/busuanzi.pure.mini.js"></script>
<script>
export default {
  name: "footerView",
  mounted() {
    this.getNowFormatDate();
    let myTimeDisplay = setInterval(() => {
      this.getNowFormatDate(); //ÊØèÁßíÊõ¥Êñ∞‰∏ÄÊ¨°Êó∂Èó¥
    }, 1000);
  },
  data() {
    return {
      day: "",
      h: "",
      min: "",
      sec: "",
    };
  },
  methods: {
    getNowFormatDate() {
      var t1 = new Date("2022/9/9 0:0:0");
      var data = new Date(); //Ëé∑ÂèñÂΩìÂâçÊó∂Èó¥
      var times = t1.getTime() - data.getTime(); //Êó∂Èó¥Â∑ÆÁöÑÊØ´ÁßíÊï∞
      var days = parseInt(times / (24 * 1000 * 3600)); //ËÆ°ÁÆóÁõ∏Â∑ÆÁöÑÂ§©Êï∞
      var leave = times % (24 * 3600 * 1000); //ËÆ°ÁÆóÂ§©Êï∞ÂêéÂâ©‰ΩôÁöÑÊØ´ÁßíÊï∞
      var h = parseInt(leave / (3600 * 1000)); //ËÆ°ÁÆóÂ∞èÊó∂Êï∞
      //ËÆ°ÁÆóÂàÜÈíüÊï∞
      var h_leave = leave % (3600 * 1000);
      var min = parseInt(h_leave / (60 * 1000));
      //ËÆ°ÁÆóÁßíÊï∞
      var min_leave = h_leave % (60 * 1000);
      var sec = parseInt(min_leave / 1000);
      this.day = days;
      this.h = h;
      this.min = min;
      this.sec = sec;
    },
  },
};
</script>

